.testingArea {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
}

.blueButtonBall {
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.8s ease-in-out;
    background-color: rgb(74, 104, 217);
    height: 200px;
    width: 200px;
    border-radius: 50%;
}

.blueButtonBall i {
    transition: all 0.4s ease-in-out;
    scale: 8;
    color: aliceblue;
}

.testingTextBox {
    height: 125px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px;
}

.blueText {
    color: rgb(74, 104, 217);
}

.textNeedWrap {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    transition: all 0.8s ease-in-out;
}

.testingButton {
    transition: all 0.8s ease-in-out;
}

.testingButtonAn {
    transform: rotate(360deg);
}

@media screen and (min-width: 600px){
    .testingButtonAn {
        scale: 1.5;
    }
}

.disappear {
    opacity: 0;
}

.bellIcon {
    transition: all 0.4s ease-in-out;
    scale: 0.5;
    opacity: 0;
    display: none;
}

.bellAppear {
    display: block;
    opacity: 1;
    position: fixed;

}

.bellIcon path {
    fill: aliceblue;
}

.textFirstLine {
    transition: all 0.4s ease-in-out;
    opacity: 1;
}