.describeBall {
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.8s ease-in-out;
    background-color: rgb(74, 104, 217);
    height: 200px;
    width: 200px;
    border-radius: 50%;
    scale: 0;
    transform: rotate(180deg);
}

.describeBallAfter {
    scale: 1;
    transform: rotate(360deg);
}

.describeBall .icofont-arrow-right{
    scale: 8;
    color: aliceblue;
}

.describeArea {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
}

.describeBox {
    height: 125px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px;
    opacity: 0;
    transition: all 0.8s ease-in-out;
}

.blueText {
    color: rgb(74, 104, 217);
}

.describeBoxAfter {
    opacity: 1;
}

.textNeedWrap {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}