.alignAndJustify {
    display: flex;
    align-items: center;
    justify-content: center;
}

.scoreDiv {
    display: flex;
    flex-direction: column;
}

/* scoreHead */
.scoreHead {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
}

.scoreHeadData {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.scoreHeadData h4 {
    margin: 10px;
    width: 300px;
    text-align: center;
}

.scoreHeadData h5 {
    margin: 10px;
    width: 300px;
    text-align: center;
}

/* scoreChart */

.scoreChart {
    width: 80vw;
    height: 350px;
}


/* scoreDetail */
.scoreDetail {
    display: flex;
    flex-direction: column;
    width: 80vw;
}

.scoreRow {
    display: flex;
    flex-direction: row;
}

.scoreRow div {
    width: 20vw;
}

.downloadBtns {
    margin: 50px;
}

.downloadBtns button {
    margin: 30px;
    border-radius: 8px;
}